import { Component, OnInit } from '@angular/core';
import { FilmsService } from '../../../services/films.service';


import { ActivatedRoute, Router, NavigationEnd, ResolveEnd } from '@angular/router';
import { Films } from '../../../../../../models/films.model';
import { Title, Meta } from '@angular/platform-browser';
import { Subscriber, Subscription } from 'rxjs';


@Component({
  selector: 'my-films-page',
  templateUrl: './films-page.component.html',
  styleUrls: ['./films-page.component.scss'],

})
export class FilmsPageComponent implements OnInit {

  constructor(
    private filmsServ: FilmsService,
    private route: ActivatedRoute,
    private router: Router,
    private title: Title,
    private meta: Meta
  ) { }


  films: Films = new Films();
  subs: Subscription
  ngOnInit() {

    this.subs = this.filmsServ.films$.subscribe(s => {
      this.films = s
      this.title.setTitle(`RexFilm.ru - ${this.filmsServ.filter$.value.type}Ñ Ð¾Ð½Ð»Ð°Ð¹Ð½.`)
      this.meta.updateTag({ property: "description", content: `RexFilm.ru - Ð¿Ð¾Ð´Ð±Ð¾Ñ ${this.filmsServ.filter$.value.type}Ð¾Ð² Ð¿Ð¾ ÐºÑÐ¸ÑÐµÑÐ¸ÑÐ¼` })

    })

  }

  async pageChanged(e) {

    this.filmsServ.setFilterPage(e)
    await this.filmsServ.getFilms().toPromise();
    window.scrollTo(0, 0)
  }

  ngOnDestroy() {
    this.subs.unsubscribe()
  }
}
